import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

function TypeDetail({ match }) {

    const [category, setCategory] = useState([])
    const [categoryName, setCategoryName] = useState({})
    const [posts, setPosts] = useState([])
    const id = match.params.id


    useEffect(() => {
        axios({
            method: "GET",
            url: 'http://worker.std-1378.ist.mospolytech.ru/api/category/'
        }).then(response => {
            setCategory(response.data)
        })
    }, [])
    useEffect(() => {
        axios({
            method: "GET",
            url: `http://worker.std-1378.ist.mospolytech.ru/api/category/${categoryName}/`
        }).then(response => {
            setPosts(response.data.posts)
        })
    }, [categoryName])

    return (
        <div className="row">
            <div className='col-md-3'>
                <ul class="list-group">
                    {category.map(p => (
                        <li key={p.id} class="list-group-item" onClick={setCategoryName(p.id)}>{p.name}</li>
                    ))}
                </ul>
                {console.log(posts)}
            </div>
            {/* <div>
                Category with id {category.id}
                <p>Category: <strong>{category.name}</strong></p>
                <hr />
                <div className="row">
                    {posts.map((p) => {
                        if (p.blog_type.id === id){
                        return (
                            <div className="col-md-4" key={p.id}>
                                <h4>{p.title}</h4>
                                <p>{p.content}</p>
                                <p>{p.id}</p>
                                <Link to={{ pathname: `/posts/${p.id}`, fromDashboard: false }}>Детали</Link>
                            </div>
                        )}else return null
                    })}
                </div>
            </div> */}
        </div>
    )
}

export default TypeDetail